import React, { useState, useEffect } from 'react';
import { Table, Form, Button } from 'react-bootstrap';
import axios from 'axios';

interface User {
  id: string;
  name: string;
  email: string;
  lastLoginTime: string;
  registrationTime: string;
  status: number; // 0 = Unverified, 1 = Active, 2 = Blocked
  lastActivityTime: string;
}

const UserTable: React.FC = () => {
  const [users, setUsers] = useState<User[]>([]);
  const [selectedUsers, setSelectedUsers] = useState<string[]>([]);
  const [selectAll, setSelectAll] = useState(false);
  const [sortField, setSortField] = useState('lastLoginTime');
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('desc');

  useEffect(() => {
    fetchUsers();
  }, [sortField, sortOrder]);

  const fetchUsers = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await axios.get('/api/users', {
        params: { sortBy: sortField, sortOrder },
        headers: { Authorization: `Bearer ${token}` },
      });
      setUsers(response.data.users);
    } catch (error) {
      console.error('Error fetching users:', error);
    }
  };

  const getUniqIdValue = (user: User) => user.id;

  const handleSelectAll = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.checked) {
      setSelectedUsers(users.map(user => getUniqIdValue(user)));
      setSelectAll(true);
    } else {
      setSelectedUsers([]);
      setSelectAll(false);
    }
  };

  const handleSelectUser = (userId: string) => {
    if (selectedUsers.includes(userId)) {
      setSelectedUsers(selectedUsers.filter(id => id !== userId));
      setSelectAll(false);
    } else {
      const newSelected = [...selectedUsers, userId];
      setSelectedUsers(newSelected);
      if (newSelected.length === users.length) setSelectAll(true);
    }
  };

  const handleBulkAction = async (action: string) => {
    try {
      const token = localStorage.getItem('token');
      await axios.post(
        '/api/users/bulk-action',
        { userIds: selectedUsers, action },
        { headers: { Authorization: `Bearer ${token}` } }
      );
      fetchUsers();
      setSelectedUsers([]);
      setSelectAll(false);
    } catch (error) {
      console.error('Error performing bulk action:', error);
    }
  };

  //      
  const getStatusText = (status: number) => {
    switch (status) {
      case 0: return 'Unverified';
      case 1: return 'Active';
      case 2: return 'Blocked';
      default: return 'Unknown';
    }
  };

  const getStatusBadgeClass = (status: number) => {
    switch (status) {
      case 0: return 'bg-warning';
      case 1: return 'bg-success';
      case 2: return 'bg-danger';
      default: return 'bg-secondary';
    }
  };

  return (
    <div className="container mt-4">
      {/* Toolbar */}
      <div className="d-flex mb-3 p-3 border rounded bg-light">
        <Button
          variant="danger"
          className="me-2"
          onClick={() => handleBulkAction('block')}
          disabled={selectedUsers.length === 0}
        >
          <i className="bi bi-lock-fill me-1"></i> Block
        </Button>

        <Button
          variant="success"
          className="me-2"
          onClick={() => handleBulkAction('unblock')}
          disabled={selectedUsers.length === 0}
        >
          <i className="bi bi-unlock-fill me-1"></i> Unblock
        </Button>

        <Button
          variant="outline-danger"
          className="me-2"
          onClick={() => handleBulkAction('delete')}
          disabled={selectedUsers.length === 0}
        >
          <i className="bi bi-trash-fill me-1"></i> Delete
        </Button>

        <Button
          variant="outline-warning"
          onClick={() => handleBulkAction('deleteUnverified')}
        >
          <i className="bi bi-person-x-fill me-1"></i> Delete Unverified
        </Button>

        <div className="ms-auto text-muted">
          Selected: {selectedUsers.length}
        </div>
      </div>

      {/* Table */}
      <Table striped bordered hover responsive>
        <thead>
          <tr>
            <th style={{ width: '50px' }}>
              <Form.Check
                type="checkbox"
                checked={selectAll}
                onChange={handleSelectAll}
              />
            </th>
            <th>Name</th>
            <th>Email</th>
            <th
              style={{ cursor: 'pointer' }}
              onClick={() => {
                setSortField('lastLoginTime');
                setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
              }}
            >
              Last Login {sortField === 'lastLoginTime' && (sortOrder === 'asc' ? '' : '')}
            </th>
            <th>Registration</th>
            <th>Status</th>
            <th>Last Activity</th>
          </tr>
        </thead>
        <tbody>
          {users.map(user => (
            <tr key={getUniqIdValue(user)}>
              <td>
                <Form.Check
                  type="checkbox"
                  checked={selectedUsers.includes(getUniqIdValue(user))}
                  onChange={() => handleSelectUser(getUniqIdValue(user))}
                />
              </td>
              <td>{user.name}</td>
              <td>{user.email}</td>
              <td>{new Date(user.lastLoginTime).toLocaleString()}</td>
              <td>{new Date(user.registrationTime).toLocaleDateString()}</td>
              <td>
                <span className={`badge ${getStatusBadgeClass(user.status)}`}>
                  {getStatusText(user.status)}
                </span>
              </td>
              <td>{new Date(user.lastActivityTime).toLocaleTimeString()}</td>
            </tr>
          ))}
        </tbody>
      </Table>
    </div>
  );
};

export default UserTable;
